		// 取消预约
		cancel(item){
			api.meeting(item.id, function(res) {
				console.log(res)
				if (res.error == 0) {
					this.$root.subscribe()
					uni.showToast({
						title: res.message,
						duration: 2000,
					});
				}
			})
		}
	}
